.start {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  //min-height: 880px;
  
  background-image: url("../images/first.jpg");
  background-size: cover;
  
  font-family: $roboto;
  
}

.start__title {
  max-width: 920px;
  padding-top: 10vh;
  margin: 0 auto;
  text-align: center;
  
  font-size: 44px;
  line-height: 1.3;
  color: rgba(black, 0.7);
}

.start__btn {
  position: absolute;
  display: block;
  width: 320px;
  height: 75px;
  margin: 0 auto;
  top: 50vh;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 20px;
  background-color: white;
  
  text-align: center;
  line-height: 75px;
  font-size: 36px;
  color: rgba(black, 0.7);
  text-decoration: none;
  font-weight: 500;
  
  box-shadow: 0 0 10px 1px rgba(white, 0.5);
  transition: all 300ms ease;
  
  &:hover,
  &:focus {
    color: rgba(black, 0.85);
    text-decoration: none;
    box-shadow: 0 0 25px 1px rgba(white, 0.5);
  }
}

@media(max-width: 1360px) {
  .start {
    background-image: url("../images/first_m.jpg");
  
  }
}

@media(max-width: 900px) {
  .start {
    background-image: url("../images/first_sm.jpg");
  
  }
}

@media(max-width: 768px) {
  .start {
    background-image: url("../images/first_xs.jpg");
  }
  
  .start__title {
    width: 100%;
    max-width: 100%;
    padding-top: 5vh;
    padding-left: 25px;
    padding-right: 25px;
    font-size: 28px;
  }
  
  .start__btn {
    width: 280px;
    height: 65px;
    line-height: 65px;
    font-size: 28px;
  }
}

@media(max-width: 380px) {
  .start__title {
    font-size: 22px;
  }
}