.questions {
  position: absolute;
  padding: 2.5% 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 100%;
  //left: 0;
  overflow-y: auto;

  font-family: $roboto;
  font-weight: 400;

  background: #141E30;
  background: linear-gradient(to bottom, #243B55, #141E30);

  transition: transform 400ms ease;
}

.questions__card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  position: relative;
  width: 100%;
  max-width: 980px;
  height: auto;
  min-height: 860px;
  margin: 0 auto;
  padding: 25px;
  overflow-y: auto;

  background-color: #fff;
  border-radius: 10%;

  font-size: 24px;
}

.questions__number {
  width: 100%;
  margin-bottom: 10px;

  font-size: 1em;
  font-weight: 300;
  text-align: center;
}

.questions__title {
  width: 100%;
  margin-bottom: 10px;

  font-size: 1.44em;
  text-align: center;
}

.questions__image {
  width: auto;
  max-width: 610px;
  height: auto;
  max-height: 410px;
  margin: 0 auto 25px;
  overflow: hidden;

  & img {
    width: 100%;
    max-width: 100%;
    height: auto;
    max-height: 100%;
  }
}

.questions__answers {
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.questions__answer {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: 45%;
  height: 100px;
  margin-top: 25px;

  background-color: rgba(238,238,238,1);
  border: 1px solid #141e30;
  outline: none;

  color: rgba(black, 0.7);
  font-size: 1em;
  text-align: center;
  border-radius: 25px;
  box-shadow: -5px -5px 15px 0 rgb(85, 85, 85);

  transition: all 300ms ease;

  &:hover,
  &:focus {
     background-color: rgb(162, 162, 162);
  }

  &:active {
     box-shadow: inset -5px -5px 15px 0 rgb(85, 85, 85);
  }
}

@media(max-width: 1100px) {
  .questions__card {
    max-width: 768px;

    font-size: 20px;
  }
}

@media(max-width: 768px) {
  .questions__card {
    max-width: 480px;

    font-size: 16px;

    border-radius: 5% 5%;
  }

  .questions__image {
    max-width: 420px;
  }

  .questions__answers {
    justify-content: center;
  }

  .questions__answer {
    width: 100%;
    height: 75px;
  }
}

