.final {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 100%;
  //min-height: 880px;
  
  background-image: url("../images/final.jpg");
  background-size: cover;
  
  font-family: $roboto;
  font-weight: 400;
  font-size: 25px;
  
  transition: transform 400ms ease;
  overflow-y: auto;
}

.final__title {
  max-width: 920px;
  padding-top: 20vh;
  margin: 0 auto;
  margin-bottom: 25px;
  text-align: center;
  
  font-size: 2.5em;
  line-height: 1.3;
  color: rgba(#5bf54e, 0.9);
}

.final__result {
  text-align: center;
  color: rgba(#5bf54e, 0.9);
  font-size: 1.5em;
}

.form-container {
  position: absolute;
  top: 90%;
  width: 100%;
  background: rgba(white, 0.5);
  
  & h2 {
    text-align: center;
    position: relative;
    
    &::after {
      content: '\2193';
      margin-left: 25px;
      font-size: 1.2em;
    }
  }
} 

@media(max-width: 1360px) {
  .final {
    background-image: url("../images/final_m.jpg");
  
  }
}


@media(max-width: 768px) {
 
  
  .final__title {
    width: 100%;
    max-width: 100%;
    padding-top: 5vh;
    padding-left: 25px;
    padding-right: 25px;
    font-size: 28px;
  }
  
  .final__btn {
    width: 280px;
    height: 65px;
    line-height: 65px;
    font-size: 28px;
  }
}

@media(max-width: 380px) {
  .final__title {
    font-size: 22px;
  }
}